.homepage {
  font-family: 'Source Sans Pro', sans-serif;
  color: #333;
}

/* Hero Section */
.hero-section {
  background-size: cover;
  background-position: center;
  position: relative;
  color: white;
  min-height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.hero-overlay {
  background-color: rgba(0, 0, 0, 0.55);
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.hero-content {
  text-align: center;
  padding: 20px;
}

.hero-content h1 {
  font-size: 36px;
  font-weight: bold;
  margin-bottom: 20px;
}

.breadcrumb {
  font-size: 14px;
  color: #e0e0e0;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.breadcrumb-home,
.breadcrumb-item,
.breadcrumb-current {
  color: #e0e0e0;
  text-decoration: none;
}

.breadcrumb-home:hover,
.breadcrumb-item:hover {
  text-decoration: underline;
}

.breadcrumb-current {
  color: #fff;
}

.breadcrumb-separator {
  color: #aaa;
}

/* Main Layout */
.main-layout {
  display: flex;
  flex-wrap: wrap;
}

