/* Main Content */
.main-content {
  flex: 1;
  padding: 0px;
}

.content-container {
  text-align: center;
}

.content-title {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 20px;
}

.content-description {
  font-size: 16px;
  line-height: 1.6;
  max-width: 800px;
  margin: 0 auto 30px;
}

/* Filter Section */
.filter-section {
  margin-bottom: 40px;
}

.filter-content {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  align-items: center;
  justify-content: center;
}

.filter-label {
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: bold;
}

.filter-controls {
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
}

.filter-group {
  min-width: 150px;
}

.form-select {
  padding: 8px;
  border-radius: 4px;
  border: 1px solid #ccc;
  font-size: 14px;
  width: 100%;
}

