.status-badge {
  padding: 4px 8px;
  border-radius: 12px;
  font-size: 12px;
}

.status-open {
  background: #d4edda;
  color: #155724;
}

.award-description {
  font-size: 14px;
  line-height: 1.5;
  margin-bottom: 15px;
  flex-grow: 1;
}

.award-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.deadline {
  font-size: 12px;
  color: #666;
}

/* Buttons */
.btn {
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  font-weight: 500;
}

.btn-outline {
  background: transparent;
  border: 1px solid #04526c;
  color: #04526c;
}

.btn-outline:hover {
  background: #04526c;
  color: #fff;
}

.btn-primary {
  background: #04526c;
  color: #fff;
  border: none;
}

.btn-primary:hover {
  background: #3b5bb3;
}

.btn-sm {
  padding: 6px 12px;
  font-size: 12px;
}

.btn-success {
  background: #28a745;
  color: #fff;
  border: none;
  margin-right: 10px;
}

.btn-success:hover {
  background: #218838;
}

.btn-lg {
  padding: 12px 24px;
  font-size: 16px;
}

/* Call to Action */
.cta-section {
  background: #f8f9fa;
  padding: 40px 20px;
  text-align: center;
}

.cta-title {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 20px;
}

.cta-description {
  font-size: 16px;
  line-height: 1.6;
  max-width: 800px;
  margin: 0 auto 20px;
}

