.close-btn {
  position: absolute;
  top: 15px;
  right: 15px;
  background: none;
  border: none;
  font-size: 20px;
  color: #666;
  cursor: pointer;
  transition: color 0.3s ease;
}

.close-btn:hover {
  color: #4f6ec7;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

/* Responsive */
@media (max-width: 768px) {
  .hero-content h1 {
    font-size: 24px;
  }

  .content-title {
    font-size: 24px;
  }

  .filter-controls {
    flex-direction: column;
    align-items: center;
  }

  .awards-grid {
    grid-template-columns: 1fr;
    padding: 10px;
  }

  .award-card {
    border-radius: 8px;
  }

  .sidebar {
    width: 100%;
    border-right: none;
    border-bottom: 1px solid #ddd;
  }

  .main-content {
    padding: 10px;
  }

  .modal-content {
    width: 95%;
    max-width: 400px;
    padding: 15px;
  }

  .modal-content h2 {
    font-size: 18px;
  }

  .modal-content p {
    font-size: 13px;
  }
}

@media (max-width: 480px) {
  .hero-content h1 {
    font-size: 20px;
  }

  .content-title {
    font-size: 20px;
  }

  .cta-title {
    font-size: 20px;
  }

  .btn-lg {
    padding: 10px 20px;
    font-size: 14px;
  }

  .awards-grid {
    gap: 15px;
    padding: 5px;
  }

  .award-card {
    border-radius: 6px;
  }

  .modal-content {
    width: 95%;
    max-width: 300px;
    padding: 10px;
  }

  .modal-content h2 {
    font-size: 16px;
  }

  .modal-content p {
    font-size: 12px;
  }
}